'use client';

import React, { useState, useEffect } from 'react';
import { useKontext } from '../react/context';
import { designTokens, typography, borderRadius, transitions, spacing } from './styles';

export interface KontextStatusProps {
  className?: string;
  connectedText?: string;
  disconnectedText?: string;
  showDisconnect?: boolean;
  disconnectClassName?: string;
  variant?: 'inline' | 'card' | 'badge';
}

/**
 * Component that shows connection status with consistent styling
 * Matches the developer-dashboard design system
 */
export function KontextStatus({
  className = '',
  connectedText = 'Personalized responses enabled',
  disconnectedText = 'Connect Gmail for personalized responses',
  showDisconnect = false,
  disconnectClassName = '',
  variant = 'inline',
}: KontextStatusProps) {
  const { isConnected, isLoading, disconnect } = useKontext();
  const [theme, setTheme] = useState<'light' | 'dark'>('light');
  const [isDisconnectHovered, setIsDisconnectHovered] = useState(false);

  // Update theme when component mounts and when theme changes
  useEffect(() => {
    const detectTheme = () => {
      const isDark =
        document.documentElement.classList.contains('dark') ||
        document.body.classList.contains('dark') ||
        (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches);
      setTheme(isDark ? 'dark' : 'light');
    };

    detectTheme();

    // Listen for theme changes
    const observer = new MutationObserver(detectTheme);
    observer.observe(document.documentElement, {
      attributes: true,
      attributeFilter: ['class'],
    });

    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    mediaQuery.addEventListener('change', detectTheme);

    return () => {
      observer.disconnect();
      mediaQuery.removeEventListener('change', detectTheme);
    };
  }, []);

  const tokens = designTokens[theme];

  // Base container styles based on variant
  const getContainerStyles = (): React.CSSProperties => {
    const baseStyles: React.CSSProperties = {
      fontFamily: typography.fontSans,
      fontSize: typography.fontSize.sm,
      display: 'flex',
      alignItems: 'center',
      gap: spacing[2],
    };

    switch (variant) {
      case 'card':
        return {
          ...baseStyles,
          backgroundColor: tokens.card,
          color: tokens.cardForeground,
          border: `1px solid ${tokens.border}`,
          borderRadius: borderRadius.lg,
          padding: spacing[4],
          boxShadow: 'none',
        };
      case 'badge':
        return {
          ...baseStyles,
          backgroundColor: isConnected ? tokens.success : tokens.muted,
          color: isConnected ? tokens.successForeground : tokens.mutedForeground,
          borderRadius: borderRadius.full,
          padding: `${spacing[1]} ${spacing[3]}`,
          fontSize: typography.fontSize.xs,
          fontWeight: typography.fontWeight.medium,
          display: 'inline-flex',
        };
      case 'inline':
      default:
        return {
          ...baseStyles,
          color: tokens.foreground,
        };
    }
  };

  const containerStyles = getContainerStyles();

  const loadingStyles: React.CSSProperties = {
    color: tokens.mutedForeground,
    fontFamily: typography.fontSans,
    fontSize: typography.fontSize.sm,
    display: 'flex',
    alignItems: 'center',
    gap: spacing[2],
  };

  const statusIndicatorStyles: React.CSSProperties = {
    color: tokens.success,
    fontWeight: typography.fontWeight.semibold,
    fontSize: variant === 'badge' ? typography.fontSize.xs : typography.fontSize.sm,
  };

  const statusTextStyles: React.CSSProperties = {
    color: isConnected
      ? variant === 'badge'
        ? tokens.successForeground
        : tokens.success
      : tokens.mutedForeground,
    fontWeight: variant === 'badge' ? typography.fontWeight.medium : typography.fontWeight.normal,
  };

  const disconnectButtonStyles: React.CSSProperties = {
    marginLeft: spacing[2],
    backgroundColor: isDisconnectHovered ? tokens.accent : 'transparent',
    color: tokens.destructive,
    fontWeight: typography.fontWeight.medium,
    padding: `${spacing[1]} ${spacing[2]}`,
    borderRadius: borderRadius.base,
    border: `1px solid ${tokens.destructive}`,
    cursor: 'pointer',
    transition: transitions.fast,
    fontFamily: typography.fontSans,
    fontSize: typography.fontSize.xs,
    outline: 'none',
  };

  // Loading spinner component
  const LoadingSpinner = () => (
    <svg
      width="14"
      height="14"
      viewBox="0 0 24 24"
      style={{
        animation: 'spin 1s linear infinite',
        color: tokens.mutedForeground,
      }}
    >
      <circle
        cx="12"
        cy="12"
        r="10"
        stroke="currentColor"
        strokeWidth="4"
        fill="none"
        strokeDasharray="32"
        strokeDashoffset="32"
      >
        <animate
          attributeName="stroke-dasharray"
          dur="2s"
          values="0 32;16 16;0 32;0 32"
          repeatCount="indefinite"
        />
        <animate
          attributeName="stroke-dashoffset"
          dur="2s"
          values="0;-16;-32;-32"
          repeatCount="indefinite"
        />
      </circle>
    </svg>
  );

  if (isLoading) {
    return (
      <>
        <div className={className} style={{ ...containerStyles, ...loadingStyles }}>
          <LoadingSpinner />
          <span>Loading...</span>
        </div>
        <style>{`
          @keyframes spin {
            from { transform: rotate(0deg); }
            to { transform: rotate(360deg); }
          }
        `}</style>
      </>
    );
  }

  if (isConnected) {
    return (
      <div className={className} style={containerStyles}>
        <span style={statusIndicatorStyles}></span>
        <span style={statusTextStyles}>{connectedText}</span>
        {showDisconnect && (
          <button
            onClick={disconnect}
            className={disconnectClassName}
            style={disconnectButtonStyles}
            onMouseEnter={() => setIsDisconnectHovered(true)}
            onMouseLeave={() => setIsDisconnectHovered(false)}
            onFocus={(e) => {
              e.currentTarget.style.outline = `2px solid ${tokens.ring}`;
              e.currentTarget.style.outlineOffset = '2px';
            }}
            onBlur={(e) => {
              e.currentTarget.style.outline = 'none';
            }}
          >
            Disconnect
          </button>
        )}
      </div>
    );
  }

  return (
    <div className={className} style={containerStyles}>
      <span style={statusTextStyles}>{disconnectedText}</span>
    </div>
  );
}
