// Helper to apply hover styles (since we can't use :hover in inline styles easily)
export const createInteractiveStyles = (
  baseStyle: React.CSSProperties,
  hoverStyle?: React.CSSProperties,
  activeStyle?: React.CSSProperties,
) => {
  return {
    base: baseStyle,
    hover: hoverStyle || baseStyle,
    active: activeStyle || hoverStyle || baseStyle,
  };
};
