import { useState, useCallback, useRef } from 'react';

export interface ModalState {
  isOpen: boolean;
  isConnecting: boolean;
}

export interface ModalActions {
  open: () => void;
  close: () => void;
  toggle: () => void;
  setConnecting: (connecting: boolean) => void;
}

export interface UseModalStateOptions {
  initialOpen?: boolean;
  onOpenChange?: (open: boolean) => void;
  onConnectingChange?: (connecting: boolean) => void;
}

/**
 * Hook for managing modal state with proper isolation between instances
 */
export function useModalState(options: UseModalStateOptions = {}): ModalState & ModalActions {
  const { initialOpen = false, onOpenChange, onConnectingChange } = options;

  const [isOpen, setIsOpenInternal] = useState(initialOpen);
  const [isConnecting, setIsConnectingInternal] = useState(false);

  // Use refs to store latest callbacks to avoid stale closures
  const onOpenChangeRef = useRef(onOpenChange);
  const onConnectingChangeRef = useRef(onConnectingChange);
  onOpenChangeRef.current = onOpenChange;
  onConnectingChangeRef.current = onConnectingChange;

  const setIsOpen = useCallback((open: boolean) => {
    setIsOpenInternal(open);
    onOpenChangeRef.current?.(open);
  }, []);

  const setIsConnecting = useCallback((connecting: boolean) => {
    setIsConnectingInternal(connecting);
    onConnectingChangeRef.current?.(connecting);
  }, []);

  const open = useCallback(() => setIsOpen(true), [setIsOpen]);
  const close = useCallback(() => setIsOpen(false), [setIsOpen]);
  const toggle = useCallback(() => setIsOpen(!isOpen), [isOpen, setIsOpen]);

  return {
    isOpen,
    isConnecting,
    open,
    close,
    toggle,
    setConnecting: setIsConnecting,
  };
}
