'use client';

import React, { useState } from 'react';
import { useKontext } from '../react/context';
import { useThemeDetection } from '../hooks/useThemeDetection';
import {
  designTokens,
  typography,
  borderRadius,
  transitions,
  buttonSizes,
  spacing,
  elevation,
} from './styles';

export interface KontextConnectButtonProps {
  className?: string;
  children?: React.ReactNode;
  variant?: 'default' | 'secondary' | 'outline' | 'ghost' | 'success';
  size?: 'sm' | 'default' | 'lg';
  fullWidth?: boolean;
  onError?: (error: string) => void;
}

/**
 * Button component to connect Gmail with built-in styling
 * Matches the developer-dashboard design system
 */
export function KontextConnectButton({
  className = '',
  children = 'Connect Gmail',
  variant = 'default',
  size = 'default',
  fullWidth = false,
  onError,
}: KontextConnectButtonProps) {
  const { connectGmail, isConnected, isLoading } = useKontext();
  const theme = useThemeDetection();
  const [isHovered, setIsHovered] = useState(false);
  const [isConnecting, setIsConnecting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [showError, setShowError] = useState(false);

  const tokens = designTokens[theme];

  if (isConnected) {
    return null;
  }

  const handleConnect = async () => {
    setIsConnecting(true);
    setError(null);
    setShowError(false);

    try {
      await connectGmail();
    } catch (err) {
      // Silently catch any errors from connectGmail
      console.debug('[KontextConnectButton] Connection error caught:', err);
      const errorMessage = 'Unable to connect. Please try again.';
      setError(errorMessage);
      setShowError(true);

      // Notify parent component if handler provided
      if (onError) {
        onError(errorMessage);
      }

      // Auto-hide error after 5 seconds
      setTimeout(() => setShowError(false), 5000);
    } finally {
      setIsConnecting(false);
    }
  };

  // Get variant styles
  const getVariantStyles = () => {
    const baseStyles: Record<string, React.CSSProperties> = {
      default: {
        backgroundColor: tokens.primary,
        color: tokens.primaryForeground,
        border: 'none',
      },
      secondary: {
        backgroundColor: tokens.secondary,
        color: tokens.secondaryForeground,
        border: 'none',
      },
      outline: {
        backgroundColor: 'transparent',
        color: tokens.foreground,
        border: `1px solid ${tokens.border}`,
      },
      ghost: {
        backgroundColor: 'transparent',
        color: tokens.foreground,
        border: 'none',
      },
      success: {
        backgroundColor: tokens.success,
        color: tokens.successForeground,
        border: 'none',
      },
    };

    const hoverStyles: Record<string, React.CSSProperties> = {
      default: {
        backgroundColor: tokens.primary,
        opacity: 0.9,
      },
      secondary: {
        backgroundColor: tokens.secondary,
        opacity: 0.8,
      },
      outline: {
        backgroundColor: tokens.accent,
      },
      ghost: {
        backgroundColor: tokens.accent,
      },
      success: {
        backgroundColor: tokens.success,
        opacity: 0.9,
      },
    };

    return {
      base: baseStyles[variant],
      hover: hoverStyles[variant],
    };
  };

  const variantStyles = getVariantStyles();
  const sizeStyles = buttonSizes[size];

  const buttonStyles: React.CSSProperties = {
    // Base styles
    fontFamily: typography.fontSans,
    fontWeight: typography.fontWeight.medium,
    borderRadius: borderRadius.lg, // Squircle shape for button
    transition: transitions.fast,
    cursor: isConnecting || isLoading ? 'not-allowed' : 'pointer',
    display: 'inline-flex',
    alignItems: 'center',
    justifyContent: 'center',
    whiteSpace: 'nowrap',
    outline: 'none',
    position: 'relative',
    width: fullWidth ? '100%' : 'auto',

    // Size styles
    ...sizeStyles,

    // Variant styles (but remove the border since we'll use box-shadow)
    ...variantStyles.base,
    border: 'none',

    // Combine tri-color outline with elevation shadow
    boxShadow: `
      0 0 0 1px ${tokens.forestGreen},
      0 0 0 2px ${tokens.zinc},
      ${theme === 'dark' ? elevation.lowDark : elevation.low}
    `,

    // Hover state (applied conditionally)
    ...(isHovered && !isConnecting && !isLoading ? variantStyles.hover : {}),

    // Disabled state
    opacity:
      isConnecting || isLoading
        ? 0.5
        : isHovered && 'opacity' in variantStyles.hover
          ? variantStyles.hover.opacity
          : 1,
  };

  // Loading spinner component - matches modal animation
  const LoadingSpinner = () => (
    <svg
      width="16"
      height="16"
      viewBox="0 0 24 24"
      style={{
        animation: 'spin 1s linear infinite',
      }}
    >
      <circle
        cx="12"
        cy="12"
        r="10"
        stroke="currentColor"
        strokeWidth="4"
        fill="none"
        strokeDasharray="32"
        strokeDashoffset="32"
      >
        <animate
          attributeName="stroke-dasharray"
          dur="2s"
          values="0 32;16 16;0 32;0 32"
          repeatCount="indefinite"
        />
        <animate
          attributeName="stroke-dashoffset"
          dur="2s"
          values="0;-16;-32;-32"
          repeatCount="indefinite"
        />
      </circle>
    </svg>
  );

  // Error tooltip styles
  const errorTooltipStyles: React.CSSProperties = {
    position: 'absolute',
    bottom: '100%',
    left: '50%',
    transform: 'translateX(-50%)',
    marginBottom: spacing[2],
    backgroundColor: tokens.destructive,
    color: tokens.destructiveForeground,
    padding: `${spacing[1]} ${spacing[2]}`,
    borderRadius: borderRadius.base,
    fontSize: typography.fontSize.xs,
    fontFamily: typography.fontSans,
    whiteSpace: 'nowrap',
    opacity: showError ? 1 : 0,
    pointerEvents: 'none',
    transition: transitions.fast,
    zIndex: 10,
  };

  return (
    <>
      <div
        style={{
          position: 'relative',
          display: 'inline-block',
          width: fullWidth ? '100%' : 'auto',
        }}
      >
        {showError && error && <div style={errorTooltipStyles}>{error}</div>}
        <button
          onClick={handleConnect}
          disabled={isConnecting || isLoading}
          className={className}
          style={buttonStyles}
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}
          onFocus={(e) => {
            // Add focus ring with increased offset to not overlap tri-color outline, keep elevation
            e.currentTarget.style.boxShadow = `
              0 0 0 1px ${tokens.forestGreen},
              0 0 0 2px ${tokens.zinc},
              0 0 0 5px ${tokens.ring},
              ${theme === 'dark' ? elevation.lowDark : elevation.low}
            `;
          }}
          onBlur={(e) => {
            // Restore original tri-color outline with elevation
            e.currentTarget.style.boxShadow = `
              0 0 0 1px ${tokens.forestGreen},
              0 0 0 2px ${tokens.zinc},
              ${theme === 'dark' ? elevation.lowDark : elevation.low}
            `;
          }}
        >
          {isConnecting || isLoading ? (
            <span style={{ display: 'flex', alignItems: 'center', gap: spacing[2] }}>
              <LoadingSpinner />
              Connecting...
            </span>
          ) : (
            children
          )}
        </button>
      </div>
      <style>{`
        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
      `}</style>
    </>
  );
}
