'use client';

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import type { ReactNode } from 'react';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { createTRPCReact } from '@trpc/react-query';
import { httpBatchLink } from '@trpc/client';
import superjson from 'superjson';
import { Persona } from '../client/index';
import type { AppRouter } from '../types/api-types';

// Create tRPC React instance (types narrowed to any to simplify public d.ts)
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const trpc = createTRPCReact<AppRouter>() as any;

interface KontextContextValue {
  apiKey?: string;
  isConnected: boolean;
  userId: string | null;
  isLoading: boolean;
  error: string | null;
  connectGmail: () => void;
  disconnect: () => void;
  // Profile caching
  systemPrompt: string | null;
  profileLastUpdated: Date | null;
  isProfileStale: boolean;
  isRefreshingProfile: boolean;
  refreshProfile: () => Promise<void>;
  // tRPC access (for advanced users)
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  trpc: any;
}

const KontextContext = createContext<KontextContextValue | undefined>(undefined);

export interface KontextProviderProps {
  apiKey?: string;
  apiUrl?: string;
  children: ReactNode;
}

// Profile cache TTL: 24 hours
const PROFILE_TTL = 24 * 60 * 60 * 1000;

/**
 * Internal component that provides the actual Persona context
 */
function KontextContextProvider({ apiKey, apiUrl, children }: KontextProviderProps) {
  const [isConnected, setIsConnected] = useState(false);
  const [userId, setUserId] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Profile cache state
  const [systemPrompt, setSystemPrompt] = useState<string | null>(null);
  const [profileLastUpdated, setProfileLastUpdated] = useState<Date | null>(null);
  const [isRefreshingProfile, setIsRefreshingProfile] = useState(false);

  // Create a single Persona instance
  const persona = React.useMemo(() => {
    if (!apiKey) return null;
    return new Persona({
      apiKey,
      apiUrl: apiUrl || 'https://api.kontext.dev',
    });
  }, [apiKey, apiUrl]);

  // Calculate if profile is stale
  const isProfileStale = profileLastUpdated
    ? Date.now() - profileLastUpdated.getTime() > PROFILE_TTL
    : true;

  // Fetch profile data
  const fetchProfile = useCallback(async () => {
    if (!userId || !apiKey) return;

    setIsRefreshingProfile(true);
    setError(null);

    try {
      if (!persona) {
        throw new Error('Persona client not initialized');
      }

      const context = await persona.getContext({
        userId,
        task: 'chat',
        maxTokens: 500,
      });

      setSystemPrompt(context.systemPrompt);
      setProfileLastUpdated(new Date());
    } catch (err) {
      console.debug('[Kontext SDK] Failed to fetch profile:', err);
      // Don't propagate profile fetch errors - they're not critical
      // Just log them for debugging
    } finally {
      setIsRefreshingProfile(false);
    }
  }, [userId, apiUrl, apiKey, persona]);

  // Check connection status by attempting to fetch Gmail data
  const checkConnection = useCallback(async () => {
    if (!apiKey) {
      setIsLoading(false);
      return;
    }

    try {
      // Get userId from URL params (OAuth callback) or local storage
      const urlParams = new URLSearchParams(window.location.search);
      const userIdFromUrl = urlParams.get('user_id');

      if (userIdFromUrl) {
        setUserId(userIdFromUrl);
        setIsConnected(true);
        // Clear URL params
        window.history.replaceState({}, document.title, window.location.pathname);
        // Store in localStorage for persistence
        // TODO: Consider implementing client-side encryption for sensitive data
        // or use sessionStorage for temporary session data
        localStorage.setItem('persona_user_id', userIdFromUrl);
        // Don't fetch profile here - let it happen after state updates
      } else {
        // Check localStorage for existing connection
        // TODO: Consider implementing client-side encryption for sensitive data
        const storedUserId = localStorage.getItem('persona_user_id');
        if (storedUserId) {
          setUserId(storedUserId);
          setIsConnected(true);
          // Profile will be fetched by the useEffect
        }
      }
    } catch (err) {
      console.debug('[Kontext SDK] Failed to check connection:', err);
      // Don't propagate initialization errors - they're not critical
    } finally {
      setIsLoading(false);
    }
  }, [apiKey, fetchProfile]);

  // Check connection on mount only
  useEffect(() => {
    checkConnection();
  }, []);

  // Fetch profile when userId changes and is connected
  useEffect(() => {
    if (userId && isConnected && !systemPrompt) {
      fetchProfile();
    }
  }, [userId, isConnected, systemPrompt, fetchProfile]);

  const connectGmail = async () => {
    if (!apiUrl || !apiKey) {
      const errorMsg = 'API URL and API key required for Gmail connection';
      setError(errorMsg);
      throw new Error(errorMsg);
    }

    try {
      // Validate and sanitize redirect URI to prevent open redirect attacks
      const currentUrl = new URL(window.location.href);
      // Only use origin + pathname, strip query params and fragments for security
      const redirectUri = `${currentUrl.origin}${currentUrl.pathname}`;

      // Additional validation: ensure redirect URI is from the same origin
      if (!redirectUri.startsWith(window.location.origin)) {
        throw new Error('Invalid redirect URI - must be from the same origin');
      }

      const response = await fetch(
        `${apiUrl}/oauth/gmail?redirect_uri=${encodeURIComponent(redirectUri)}`,
        {
          method: 'GET',
          headers: {
            'x-api-key': apiKey, // API key in header, not URL
          },
          // Remove redirect: 'manual' - let the server send back the URL
        },
      ).catch((fetchErr) => {
        // Handle network errors gracefully
        console.debug('[Kontext SDK] Network error during OAuth:', fetchErr);
        throw new Error('Network error. Please check your connection and try again.');
      });

      if (response.ok) {
        const data = await response.json().catch(() => null);
        if (data?.authUrl) {
          window.location.href = data.authUrl;
        } else {
          throw new Error('No authorization URL received from server');
        }
      } else {
        const errorData = await response
          .json()
          .catch(() => ({ error: { message: 'Failed to initiate OAuth' } }));
        throw new Error(errorData.error?.message || 'Failed to initiate OAuth');
      }
    } catch (err) {
      const errorMessage =
        err instanceof Error ? err.message : 'Failed to initiate Gmail connection';
      console.debug('[Kontext SDK] Failed to initiate Gmail connection:', err);
      setError(errorMessage);
      // Re-throw to let the modal handle the error display
      throw err;
    }
  };

  const disconnect = async () => {
    if (!userId || !persona) {
      const errorMsg = 'Must be connected to disconnect';
      setError(errorMsg);
      throw new Error(errorMsg);
    }

    try {
      // Use the SDK's disconnect method
      await persona.disconnect(userId);
    } catch (err) {
      console.debug('[Kontext SDK] Failed to disconnect:', err);
      // Don't set error state for disconnect failures
      // We'll still clear local state below
    } finally {
      // Always clear local state and storage, even if API call fails
      // This ensures the UI properly updates to disconnected state
      setUserId(null);
      setIsConnected(false);
      setSystemPrompt(null);
      setProfileLastUpdated(null);
      setError(null); // Clear any previous errors
      localStorage.removeItem('persona_user_id');
    }
  };

  // Manual profile refresh
  const refreshProfile = useCallback(async () => {
    if (!isConnected || !userId) {
      throw new Error('User must be connected to refresh profile');
    }
    await fetchProfile();
  }, [isConnected, userId, fetchProfile]);

  const value: KontextContextValue = {
    apiKey,
    isConnected,
    userId,
    isLoading,
    error,
    connectGmail,
    disconnect,
    // Profile cache
    systemPrompt,
    profileLastUpdated,
    isProfileStale,
    isRefreshingProfile,
    refreshProfile,
    // tRPC access
    trpc,
  };

  return <KontextContext.Provider value={value}>{children}</KontextContext.Provider>;
}

/**
 * KontextProvider component that wraps your app
 * Automatically includes tRPC and QueryClient setup internally
 * Similar to PrivyProvider but for Persona
 */
export function KontextProvider({
  apiKey,
  apiUrl = 'https://api.kontext.dev',
  children,
}: KontextProviderProps) {
  // Create stable instances
  const [queryClient] = useState(
    () =>
      new QueryClient({
        defaultOptions: {
          queries: {
            staleTime: 10 * 60 * 1000, // 10 minutes for most data
            gcTime: 30 * 60 * 1000, // 30 minutes in cache (was cacheTime in older versions)
            retry: (failureCount, error: unknown) => {
              // Don't retry on 4xx client errors
              const err = error as { status?: number };
              if (err?.status && err.status >= 400 && err.status < 500) return false;
              // Retry up to 3 times with exponential backoff
              return failureCount < 3;
            },
            refetchOnWindowFocus: false, // Avoid unnecessary refetches
            refetchOnReconnect: true, // Refetch when network reconnects
          },
          mutations: {
            retry: 1, // Only retry mutations once
          },
        },
      }),
  );

  const [trpcClient] = useState(() => {
    if (!apiKey) return null;

    // Create tRPC client with API key authentication
    return trpc.createClient({
      transformer: superjson,
      links: [
        httpBatchLink({
          url: `${apiUrl}/trpc`,
          maxItems: 10, // Maximum number of operations in a single batch
          maxURLLength: 2083, // Prevent URLs from becoming too long
          async headers() {
            // Pass API key in x-api-key header (more semantic for API keys)
            return {
              'x-api-key': apiKey,
            };
          },
          fetch(url, options) {
            return fetch(url, {
              ...options,
              // Add connection pooling for better performance
              keepalive: true,
            });
          },
        }),
      ],
    });
  });

  // Always wrap with tRPC and QueryClient providers when apiKey is available
  if (trpcClient) {
    return (
      <trpc.Provider client={trpcClient} queryClient={queryClient}>
        <QueryClientProvider client={queryClient}>
          <KontextContextProvider apiKey={apiKey} apiUrl={apiUrl}>
            {children}
          </KontextContextProvider>
        </QueryClientProvider>
      </trpc.Provider>
    );
  }

  // If no apiKey, just provide the basic KontextContext with QueryClient
  return (
    <QueryClientProvider client={queryClient}>
      <KontextContextProvider apiKey={apiKey} apiUrl={apiUrl}>
        {children}
      </KontextContextProvider>
    </QueryClientProvider>
  );
}

/**
 * Hook to access Persona context
 * Must be used within KontextProvider
 */
export function useKontext() {
  const context = useContext(KontextContext);
  if (!context) {
    throw new Error('useKontext must be used within KontextProvider');
  }
  return context;
}

/**
 * Hook to access just the profile data
 * Convenience hook for getting cached profile information
 */
export function useKontextProfile() {
  const context = useKontext();
  return {
    systemPrompt: context.systemPrompt,
    lastUpdated: context.profileLastUpdated,
    isStale: context.isProfileStale,
    isRefreshing: context.isRefreshingProfile,
    refresh: context.refreshProfile,
  };
}
