import { useState, useEffect, useRef } from 'react';

export type Theme = 'light' | 'dark';

export interface UseThemeDetectionOptions {
  initialTheme?: Theme;
  onThemeChange?: (theme: Theme) => void;
}

/**
 * Hook for detecting and tracking theme changes
 * Each instance maintains its own observers to prevent conflicts
 */
export function useThemeDetection(options: UseThemeDetectionOptions = {}): Theme {
  const { initialTheme = 'light', onThemeChange } = options;
  const [theme, setTheme] = useState<Theme>(initialTheme);

  // Refs for cleanup tracking
  const observerRef = useRef<MutationObserver | null>(null);
  const mediaQueryRef = useRef<MediaQueryList | null>(null);
  const detectThemeRef = useRef<(() => void) | null>(null);
  const onThemeChangeRef = useRef(onThemeChange);
  onThemeChangeRef.current = onThemeChange;

  useEffect(() => {
    const detectTheme = () => {
      const isDark =
        document.documentElement.classList.contains('dark') ||
        document.body.classList.contains('dark') ||
        (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches);

      const newTheme = isDark ? 'dark' : 'light';
      setTheme(newTheme);
      onThemeChangeRef.current?.(newTheme);
    };

    // Store ref for cleanup
    detectThemeRef.current = detectTheme;

    // Initial theme detection
    detectTheme();

    // Set up DOM observer for class changes
    const observer = new MutationObserver(detectTheme);
    observerRef.current = observer;
    observer.observe(document.documentElement, {
      attributes: true,
      attributeFilter: ['class'],
    });

    // Set up media query listener for system theme changes
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    mediaQueryRef.current = mediaQuery;
    mediaQuery.addEventListener('change', detectTheme);

    // Cleanup function
    return () => {
      if (observerRef.current) {
        observerRef.current.disconnect();
        observerRef.current = null;
      }
      if (mediaQueryRef.current && detectThemeRef.current) {
        mediaQueryRef.current.removeEventListener('change', detectThemeRef.current);
        mediaQueryRef.current = null;
      }
      detectThemeRef.current = null;
    };
  }, []); // Empty dependency array - we want this to run once per hook instance

  return theme;
}
