'use client';

import React, { useId } from 'react';
import { useFloating, useClick, useRole, useInteractions } from '@floating-ui/react';
import { useKontext, useKontextProfile } from '../react/context';
import { useModalState } from '../hooks/useModalState';
import { useThemeDetection } from '../hooks/useThemeDetection';
import {
  spacing,
  typography,
  borderRadius,
  transitions,
  buttonSizes,
  designTokens,
} from './styles';

export interface KontextModalProps {
  trigger: React.ReactNode;
  className?: string;
  apiKey?: string;
  apiUrl?: string;
  id?: string; // Optional custom ID for the modal
}

export function KontextModal({ trigger, className = '', id: customId }: KontextModalProps) {
  // Generate unique ID for this modal instance
  const generatedId = useId();
  const modalId = customId || generatedId;

  // Use custom hooks for isolated state management
  const { isOpen, isConnecting, open, close, setConnecting } = useModalState();
  const theme = useThemeDetection();

  const { isConnected, connectGmail, disconnect, isLoading: personaLoading } = useKontext();
  const profile = useKontextProfile();

  const [localError, setLocalError] = React.useState<string | null>(null);
  const [showError, setShowError] = React.useState(false);

  const tokens = designTokens[theme];

  const handleConnectGmail = async () => {
    setConnecting(true);
    setLocalError(null);
    setShowError(false);
    try {
      await connectGmail();
    } catch (err) {
      // Silently catch any errors from connectGmail
      console.debug('[KontextModal] Connection error caught:', err);
      setLocalError('Unable to connect at this time. Please try again later.');
      setShowError(true);
      // Auto-hide error after 5 seconds
      setTimeout(() => setShowError(false), 5000);
    } finally {
      setConnecting(false);
    }
  };

  const handleDisconnect = async () => {
    try {
      await disconnect();
    } catch (err) {
      // Silently catch any errors from disconnect
      console.debug('[KontextModal] Disconnect error caught:', err);
      // Still close modal even if disconnect fails
    }
  };

  // Helper function to format relative time
  const formatRelativeTime = (date: Date): string => {
    const seconds = Math.floor((Date.now() - date.getTime()) / 1000);

    if (seconds < 60) return 'just now';
    const minutes = Math.floor(seconds / 60);
    if (minutes < 60) return `${minutes} minute${minutes === 1 ? '' : 's'} ago`;
    const hours = Math.floor(minutes / 60);
    if (hours < 24) return `${hours} hour${hours === 1 ? '' : 's'} ago`;
    const days = Math.floor(hours / 24);
    return `${days} day${days === 1 ? '' : 's'} ago`;
  };

  const { refs, context } = useFloating({
    open: isOpen,
    onOpenChange: (isModalOpen) => {
      if (isModalOpen) {
        open();
      } else {
        close();
      }
    },
  });

  const click = useClick(context);
  const role = useRole(context, {
    role: 'dialog',
  });

  const { getReferenceProps } = useInteractions([click, role]);

  const modalContentStyles: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    gap: spacing[3], // Reduced gap for tighter spacing
  };

  const headingStyles: React.CSSProperties = {
    fontSize: typography.fontSize.base, // Reduced from lg to base
    fontWeight: typography.fontWeight.semibold,
    margin: 0,
    marginTop: '-7px', // Move up by 7px
    color: tokens.foreground,
    fontFamily: typography.fontSans,
    textAlign: 'center', // Center align to match description
  };

  const loadingStyles: React.CSSProperties = {
    display: 'flex',
    justifyContent: 'center',
    color: tokens.mutedForeground,
    fontFamily: typography.fontSans,
    fontSize: typography.fontSize.sm,
  };

  const statusContainerStyles: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    gap: spacing[3],
  };

  const statusRowStyles: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    gap: spacing[2],
    fontFamily: typography.fontSans,
    fontSize: typography.fontSize.sm,
  };

  const successIndicatorStyles: React.CSSProperties = {
    color: tokens.success,
    fontWeight: typography.fontWeight.semibold,
  };

  const successTextStyles: React.CSSProperties = {
    color: tokens.success,
    fontWeight: typography.fontWeight.medium,
  };

  const profileStatusStyles: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    gap: spacing[2],
    fontSize: typography.fontSize.sm,
    fontFamily: typography.fontSans,
  };

  const profileTimeStyles: React.CSSProperties = {
    color: tokens.mutedForeground,
  };

  const refreshButtonStyles: React.CSSProperties = {
    background: 'none',
    border: 'none',
    color: tokens.primary,
    cursor: 'pointer',
    fontSize: typography.fontSize.sm,
    padding: 0,
    textDecoration: 'underline',
    fontFamily: typography.fontSans,
    transition: transitions.fast,
  };

  const disconnectButtonStyles: React.CSSProperties = {
    ...buttonSizes.default,
    backgroundColor: tokens.secondary,
    color: tokens.secondaryForeground,
    fontWeight: typography.fontWeight.normal,
    padding: `${spacing[2]} ${spacing[4]}`,
    borderRadius: borderRadius.lg,
    border: `1px solid ${tokens.zinc}`,
    cursor: 'pointer',
    transition: transitions.base,
    width: '100%',
    fontFamily: typography.fontSans,
    fontSize: typography.fontSize.sm,
    boxShadow: `0 0 12px rgba(255, 255, 255, 0.15)`, // Light white glow
  };

  const descriptionStyles: React.CSSProperties = {
    margin: 0,
    marginTop: '-15px', // Move up by 15px
    marginBottom: spacing[3],
    padding: `0 ${spacing[2]}`, // Add horizontal padding for breathing room
    color: tokens.mutedForeground,
    fontSize: typography.fontSize.sm,
    lineHeight: typography.lineHeight.relaxed, // More relaxed line height
    fontFamily: typography.fontSans,
    textAlign: 'center', // Center align the text
  };

  const connectButtonStyles: React.CSSProperties = {
    width: '100%',
    backgroundColor: tokens.success,
    color: tokens.successForeground,
    fontWeight: typography.fontWeight.medium,
    padding: `${spacing[3]} ${spacing[4]}`,
    borderRadius: borderRadius.lg,
    border: `1px solid ${tokens.zinc}`,
    cursor: isConnecting ? 'not-allowed' : 'pointer',
    transition: transitions.base,
    fontFamily: typography.fontSans,
    fontSize: typography.fontSize.sm,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    opacity: isConnecting ? 0.7 : 1,
    boxShadow: `0 0 12px rgba(255, 255, 255, 0.15)`, // Light white glow
  };

  const spinnerStyles: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    gap: spacing[2],
  };

  const errorStyles: React.CSSProperties = {
    backgroundColor: `${tokens.destructive}15`,
    border: `1px solid ${tokens.destructive}30`,
    borderRadius: borderRadius.md,
    padding: `${spacing[2]} ${spacing[3]}`,
    marginTop: spacing[2],
    color: tokens.destructive,
    fontSize: typography.fontSize.sm,
    fontFamily: typography.fontSans,
    display: showError ? 'block' : 'none',
  };

  return (
    <>
      <div ref={refs.setReference} {...getReferenceProps()}>
        {trigger}
      </div>
      {isOpen && (
        <div
          style={{
            marginTop: spacing[3],
            padding: spacing[4],
            borderRadius: borderRadius.lg,
            backgroundColor: tokens.card,
            border: `1px solid ${tokens.border}`,
          }}
          className={className}
          role="dialog"
          aria-labelledby={`kontext-modal-title-${modalId}`}
          aria-describedby={`kontext-modal-description-${modalId}`}
        >
          <div style={modalContentStyles}>
            <h2 id={`kontext-modal-title-${modalId}`} style={headingStyles}>
              Kontext Connection
            </h2>

            {personaLoading ? (
              <div style={loadingStyles}>
                <span>Loading...</span>
              </div>
            ) : isConnected ? (
              <div style={statusContainerStyles}>
                <div style={statusRowStyles}>
                  <span style={successIndicatorStyles}></span>
                  <span style={successTextStyles}>Personalized responses enabled</span>
                </div>

                {profile.lastUpdated && (
                  <div style={profileStatusStyles}>
                    <span
                      style={{
                        fontSize: '1rem',
                        color: profile.isStale ? tokens.warning : tokens.success,
                      }}
                    >
                      {profile.isStale ? '' : ''}
                    </span>
                    <span style={profileTimeStyles}>
                      Profile updated {formatRelativeTime(profile.lastUpdated)}
                    </span>
                    <button
                      onClick={() => profile.refresh()}
                      disabled={profile.isRefreshing}
                      style={{
                        ...refreshButtonStyles,
                        color: profile.isRefreshing ? tokens.mutedForeground : tokens.primary,
                        cursor: profile.isRefreshing ? 'not-allowed' : 'pointer',
                        textDecoration: profile.isRefreshing ? 'none' : 'underline',
                      }}
                      onMouseEnter={(e) => {
                        if (!profile.isRefreshing) {
                          e.currentTarget.style.opacity = '0.8';
                        }
                      }}
                      onMouseLeave={(e) => {
                        e.currentTarget.style.opacity = '1';
                      }}
                    >
                      {profile.isRefreshing ? 'Refreshing...' : 'Refresh'}
                    </button>
                  </div>
                )}

                <button
                  onClick={handleDisconnect}
                  style={disconnectButtonStyles}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.opacity = '0.8';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.opacity = '1';
                  }}
                >
                  Disconnect
                </button>
              </div>
            ) : (
              <div style={statusContainerStyles}>
                <p id={`kontext-modal-description-${modalId}`} style={descriptionStyles}>
                  Connect your Gmail to enable personalized AI responses based on your communication
                  patterns and preferences.
                </p>
                <button
                  onClick={handleConnectGmail}
                  disabled={isConnecting}
                  style={connectButtonStyles}
                  onMouseEnter={(e) => {
                    if (!isConnecting) {
                      e.currentTarget.style.opacity = '0.9';
                    }
                  }}
                  onMouseLeave={(e) => {
                    if (!isConnecting) {
                      e.currentTarget.style.opacity = '1';
                    }
                  }}
                >
                  {isConnecting ? (
                    <span style={spinnerStyles}>
                      <svg
                        width="16"
                        height="16"
                        viewBox="0 0 24 24"
                        style={{ animation: 'spin 1s linear infinite' }}
                      >
                        <circle
                          cx="12"
                          cy="12"
                          r="10"
                          stroke="currentColor"
                          strokeWidth="4"
                          fill="none"
                          strokeDasharray="32"
                          strokeDashoffset="32"
                        >
                          <animate
                            attributeName="stroke-dasharray"
                            dur="2s"
                            values="0 32;16 16;0 32;0 32"
                            repeatCount="indefinite"
                          />
                          <animate
                            attributeName="stroke-dashoffset"
                            dur="2s"
                            values="0;-16;-32;-32"
                            repeatCount="indefinite"
                          />
                        </circle>
                      </svg>
                      Connecting...
                    </span>
                  ) : (
                    'Connect Gmail for Personalization'
                  )}
                </button>
                {localError && <div style={errorStyles}>{localError}</div>}
              </div>
            )}
          </div>
        </div>
      )}
      <style>{`
        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
      `}</style>
    </>
  );
}
